import React, { useState, useEffect } from 'react';
import { invoke } from '@forge/bridge';
import './App.css';

function App() {
  const [settings, setSettings] = useState({
    appName: 'HMG Index',
    enableNotifications: true,
    organizationSync: false,
    maxNotices: 10
  });
  
  const [message, setMessage] = useState('');

  useEffect(() => {
    //  
    loadSettings();
  }, []);

  const loadSettings = async () => {
    try {
      //   Forge Storage  
      console.log('Loading settings...');
      setMessage(' .');
    } catch (error) {
      setMessage('    .');
    }
  };

  const handleSettingChange = (key, value) => {
    setSettings(prev => ({
      ...prev,
      [key]: value
    }));
  };

  const saveSettings = async () => {
    try {
      //   Forge Storage  
      console.log('Saving settings:', settings);
      setMessage(' .');
    } catch (error) {
      setMessage('    .');
    }
  };

  return (
    <div className="admin-app">
      <div className="admin-container">
        <header className="admin-header">
          <h1> HMG Index  </h1>
          <p>     .</p>
        </header>

        <main className="admin-content">
          {message && (
            <div className="message-box">
              {message}
            </div>
          )}

          <div className="settings-section">
            <h2> </h2>
            
            <div className="setting-item">
              <label htmlFor="appName"> </label>
              <input
                type="text"
                id="appName"
                value={settings.appName}
                onChange={(e) => handleSettingChange('appName', e.target.value)}
                placeholder="  "
              />
              <small>Confluence   .</small>
            </div>

            <div className="setting-item">
              <label htmlFor="maxNotices">  </label>
              <input
                type="number"
                id="maxNotices"
                value={settings.maxNotices}
                onChange={(e) => handleSettingChange('maxNotices', parseInt(e.target.value))}
                min="1"
                max="50"
              />
              <small>     .</small>
            </div>
          </div>

          <div className="settings-section">
            <h2> </h2>
            
            <div className="setting-item">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={settings.enableNotifications}
                  onChange={(e) => handleSettingChange('enableNotifications', e.target.checked)}
                />
                <span className="checkmark"></span>
                  
              </label>
              <small>      .</small>
            </div>

            <div className="setting-item">
              <label className="checkbox-label">
                <input
                  type="checkbox"
                  checked={settings.organizationSync}
                  onChange={(e) => handleSettingChange('organizationSync', e.target.checked)}
                />
                <span className="checkmark"></span>
                   
              </label>
              <small>     .</small>
            </div>
          </div>

          <div className="admin-actions">
            <button className="save-btn" onClick={saveSettings}>
                
            </button>
            <button className="reset-btn" onClick={loadSettings}>
                
            </button>
          </div>

          <div className="info-section">
            <h3>  </h3>
            <div className="info-grid">
              <div className="info-item">
                <span className="info-label"> :</span>
                <span className="info-value">1.1.10</span>
              </div>
              <div className="info-item">
                <span className="info-label"> :</span>
                <span className="info-value">2025-06-25</span>
              </div>
              <div className="info-item">
                <span className="info-label"> :</span>
                <span className="info-value">-</span>
              </div>
              <div className="info-item">
                <span className="info-label"> :</span>
                <span className="info-value">Confluence Space</span>
              </div>
            </div>
          </div>
        </main>
      </div>
    </div>
  );
}

export default App;